import React, { useState } from 'react'
import SuperEditableSpan from './common/c4-SuperEditableSpan/SuperEditableSpan'
import { restoreState, saveState } from './localStorage/localStorage'
import s2 from '../../s1-main/App.module.css'
import SuperButton from '../hw04/common/c2-SuperButton/SuperButton'
import s from './HW6.module.css'

/*
 * 1 -   SuperEditableSpan.tsx    onEnterCallback, onBlurCallback, onDoubleClickCallBack
 * 2 -    restore
 * 3 -      
 */

const HW6 = () => {
    const [value, setValue] = useState<string>('')

    const save = () => {
        saveState<string>('hw6-editable-span-value', value)
    }
    const restore = () => {
        //  
        const restoredValue = restoreState<string>('hw6-editable-span-value', '')
        setValue(restoredValue)
    }

    return (
        <div id={'hw6'}>
            <div className={s2.hwTitle}>Hometask  6</div>

            <div className={s2.line}></div> {/*   */}

            {/*  :*/}
            <div className={s2.hw}>
                <div className={s.editableSpanContainer}>
                    <SuperEditableSpan
                        id={'hw6-spanable-input'}
                        value={value}
                        onChangeText={setValue}
                        spanProps={{
                            id: 'hw6-editable-span',
                            defaultText: 'Edit text',
                        }}
                    />
                </div>

                <div className={s.buttonsContainer}>
                    <SuperButton id={'hw6-save'} onClick={save}>
                        Save to ls
                    </SuperButton>
                    <SuperButton
                        id={'hw6-restore'}
                        onClick={restore}
                        xType={'secondary'}
                    >
                        Get from ls
                    </SuperButton>
                </div>
            </div>

            <div className={s2.line}></div> {/*   */}
        </div>
    )
}

export default HW6
