import React, {type ReactNode, useState} from 'react'
import SuperButton from '../hw04/common/c2-SuperButton/SuperButton'
import {restoreState} from '../hw06/localStorage/localStorage'
import s from './Clock.module.css'

function Clock() {
    const [timerId, setTimerId] = useState<number | undefined>(undefined)
    // for autotests //   //      ,     
    const [date, setDate] = useState<Date>(new Date(restoreState('hw9-date', Date.now())))
    const [show, setShow] = useState<boolean>(false)

    const start = () => {
        //   //   (   ,   +1)
        //    (https://learn.javascript.ru/settimeout-setinterval#setinterval)
        stop()
        const timerId: number = window.setInterval(() => {
            setDate(new Date())
        },1000)
        setTimerId(timerId)
    }

    const stop = () => {
        console.log('stop' ,timerId)
        //   //    ,    (timerId <- undefined)
        if (timerId !== undefined) {
            clearInterval(timerId); //  
            setTimerId(undefined); //  id 
        }
    }

    const onMouseEnter = () => { //   //     
        setShow(true)
    }
    const onMouseLeave = () => { //   //      
        setShow(false)
    }

    // const getTwoDigitsString = (num: number) => num.toString().padStart(2, '0');
    const getTwoDigitsString = (num: number) => num < 10
        ? "0" + num
        : num

    // const stringTime = 'date->time' || <br/> // 24:: (01:02:03)/(23:02:03)/(24:00:00)/(00:00:01) //  
    // const stringTime = `${String(date.getHours()).padStart(2, '0')}:`
    const stringTime = `${getTwoDigitsString(date.getHours())}:${getTwoDigitsString(date.getMinutes())}:${getTwoDigitsString(date.getSeconds())}` || <br/>

    // const stringDate = 'date->date' || <br/> // .. (01.02.2022) //  ,  01.02.0123/01.02.-123/01.02.12345  
    const stringDate = `${getTwoDigitsString(date.getDate())}.${getTwoDigitsString(date.getMonth() + 1)}.${date.getFullYear()}` || <br/>

    //    ,    (https://learn.javascript.ru/intl#intl-datetimeformat)
    // const stringDay = 'date->day' || <br/> //  
    const stringDay = new Intl.DateTimeFormat("en-US", {weekday: "long"}).format(date) || <br/>

    // const stringMonth = 'date->month' || <br/> //  
    const stringMonth = new Intl.DateTimeFormat("en-US", {month: "long"}).format(date) || <br/>

    return (
        <div className={s.clock}>
            <div
                id={'hw9-watch'}
                className={s.watch}
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
            >
                <span id={'hw9-day'}>{stringDay}</span>,{' '}
                <span id={'hw9-time'}>
                    <strong>{stringTime}</strong>
                </span>
            </div>

            <div id={'hw9-more'}>
                <div className={s.more}>
                    {show ? (
                        <>
                            <span id={'hw9-month'}>{stringMonth}</span>,{' '}
                            <span id={'hw9-date'}>{stringDate}</span>
                        </>
                    ) : (
                        <>
                            <br/>
                        </>
                    )}
                </div>
            </div>

            <div className={s.buttonsContainer}>
                <SuperButton
                    id={'hw9-button-start'}
                    // disabled={false} //   //    
                    disabled={timerId !== undefined} //   //    
                    onClick={start}
                >
                    start
                </SuperButton>
                <SuperButton
                    id={'hw9-button-stop'}
                    // disabled={false} //   //     
                    disabled={timerId === undefined} //   //     
                    onClick={stop}
                >
                    stop
                </SuperButton>
            </div>
        </div>
    )
}

export default Clock
